html {
  display: flex;
  box-sizing: border-box;
  height: 100%;
  background-color: black;
}

body {
  display: flex;
  box-sizing: border-box;
  width: 75%;
  height: 100%;
  margin: auto;
  font-family: 'Merienda', cursive;
  box-shadow: 0px 0px 30px lime;
  background-image: url('fire.jpg');
  background-size: 100%;
  background-position: center;
  background-repeat: no-repeat;
}

aside {
  box-sizing: border-box;
  width: 20%;
  height: 100%;
}

.nav {
  box-sizing: border-box;
  width: 100%;
  height: 70%;
  color: lime;
  font-size: 24px;
  // -webkit-text-stroke-width: 1px;
  // -webkit-text-stroke-color: white;
  text-shadow: 1px 1px 5px darkorange;
  
}

.board {
  box-sizing: border-box;
  width: 80%;
  height: 100%;
  user-select: none;
}

.play-area {
  display: flex;
  justify-content: space-around;
  align-items: center;
  box-sizing: border-box;
  width: 100%;
  height: 40%;
}

.hand {
  display: flex;
  justify-content: space-evenly;
  box-sizing: border-box;
  width: 100%;
  height: 30%;
  text-shadow: 1px 1px gray;
  border: 1px solid lime;
  border-radius: 1.5%;
  box-shadow: 0 0 30px lime;
}

.pile-area {
  display: flex;
  box-sizing: border-box;
  width: 100%;
  height: 30%;
  user-select: none;
}

.slot {
  box-sizing: border-box;
  height: 175px;
  width: 125px;
  margin: auto;
}

.card, .stack, .pile, .reserve {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  height: 100%;
  width: 100%;
  background-color: white;
  border: 1px solid lime;
  box-shadow: 0 0 10px lime;
  border-radius: 10%;
  font-size: 80px;
}

.pile, .reserve {
  font-family: 'Merienda', cursive;
  // font-family:;
  color: lime;
  -webkit-text-stroke-width: 2px;
  -webkit-text-stroke-color: black;
  text-shadow: 0 0 10px lime;
  border-color: lime;
  border-right-width: 5px;
  border-bottom-width: 5px;
  background-image: url('./flame.jpg');
  background-position: center;
  background-size: 200%;
  background-repeat: no-repeat;
  box-shadow: 0 0 30px lime;
}

.card::before,
.card::after {
  position: absolute;
  content: attr(data-value) attr(data-suit);
  font-size: 40px;
}

.card::before {
  top: 10px;
  left: 10px;
}

.card::after {
  bottom: 10px;
  right: 10px;
  transform: rotate(180deg);
}

.hand-2 .card {
  background-image: url('./flame.jpg');
  background-position: center;
  background-size: 200%;
  background-repeat: no-repeat;
  font-size: 0;
}

.hand-2 .card::before,
.hand-2 .card::after {
  font-size: 0;
}

.red {
  color: red;
}

.black {
  color: black;
}

.selected {
  outline: none;
  position: relative;
  bottom: 15px;
  box-shadow: 0px 0px 50px cyan;
}

.hoverable:hover {
  outline: none;
  box-shadow: 0px 0px 50px cyan;
  cursor: pointer;
}
